body {
    margin : 0;
    padding: 0;
}

.nav {
    height     : 70px;
    width      : 100%;
    display    : -webkit-flex;
    display    : flex;
    -webkit-align-items: center;
            align-items: center;
    margin     : 0;
}

.searchInput {
    height: 20px;
    width : 20vw;
    border: none;
}

.left {
    padding-left   : 16px;
    font-size      : 7vw;
    font-family    : Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    display        : -webkit-flex;
    display        : flex;
    -webkit-flex           : 50% 1;
            flex           : 50% 1;
    -webkit-align-items    : center;
            align-items    : center;
    color          : black;
    text-decoration: none;
}

.leftLeft {
    display: -webkit-flex;
    display: flex;
    -webkit-flex   : 30% 1;
            flex   : 30% 1;
}

.leftRight {
    display: -webkit-flex;
    display: flex;
    -webkit-flex   : 70% 1;
            flex   : 70% 1;
}

.right {
    display        : -webkit-flex;
    display        : flex;
    -webkit-flex           : 50% 1;
            flex           : 50% 1;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items    : center;
            align-items    : center;
}

.rightRight {
    display        : -webkit-flex;
    display        : flex;
    -webkit-flex           : 40% 1;
            flex           : 40% 1;
    -webkit-align-items    : center;
            align-items    : center;
    -webkit-justify-content: space-around;
            justify-content: space-around
}

.rightLeft {
    display        : -webkit-flex;
    display        : flex;
    -webkit-flex           : 60% 1;
            flex           : 60% 1;
    -webkit-align-items    : center;
            align-items    : center;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}



.nav2 {
    padding-left: 12px;
    height      : 30px;
    width       : 100%;
    background  : rgb(2, 0, 36);
    background  : linear-gradient(90deg, rgba(2, 0, 36, 1) 0%, rgba(148, 147, 147, 1) 37%, rgba(0, 212, 255, 1) 200%);
}

.category {
    height         : 100%;
    display        : -webkit-flex;
    display        : flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items    : center;
            align-items    : center;
    margin-left    : 30%;
    margin-right   : 30%;
    font-size      : 1vw;
}

.mButton {
    height   : 100%;
    width    : 2vw;
    font-size: 34px;
}
.firstClothDiv {
    height: 50%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
